/* =================================== */
/*	Basic Style
/* =================================== */

body {
   background-color: #fff;
   font-family: "Open Sans", sans-serif;
   font-size: 14px;
   color: #6a737b;
}

ol,
ul {
   margin: 0;
   padding: 0;
   list-style: none;
}

figure,
p {
   margin: 0;
}

a {
   color: #fff;

   -webkit-transition: all 0.3s ease-in 0s;
   -moz-transition: all 0.3s ease-in 0s;
   -ms-transition: all 0.3s ease-in 0s;
   -o-transition: all 0.3s ease-in 0s;
   transition: all 0.3s ease-in 0s;
}

iframe {
   border: 0;
}

a,
a:focus,
a:hover {
   text-decoration: none;
   outline: 0;
}

a:focus,
a:hover {
   color: #f7e27e;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   font-weight: normal;
   margin: 0;
}

.clear:before,
.clear:after {
   content: " ";
   display: table;
}

.clear:after {
   clear: both;
}

.clear {
   *zoom: 1;
}

span.color {
   color: #0feb9e;
}

body > section {
   /* padding: 70px 0; */
   margin: -1px 0;
}
.footer {
   background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
   color: #fff;
   margin: -1px 0;
   padding: 10px 0;
}
.sec-title {
}

.sec-title h2 {
   color: #f7e27e;
   font-size: 28px;
   font-weight: 800;
   text-transform: uppercase;
}

.sec-sub-title {
