}

.sec-sub-title p {
   line-height: 24px;
   font-size: 14px;
   color: #5b646e;
}

.sec-sub-title h4 {
   color: #f7e27e;
   margin: 2em 0 0.5em 0;
   font-weight: 600;
   font-size: 21px;
}

.devider {
   margin-top: 30px;
}

.devider i {
   color: #cccccc;
}

.devider:before,
.devider:after {
   content: "__________________";
   color: #e6e8ea;
