/* Steps */
.disc-list {
   font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui,
      helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
   counter-reset: step;
   /* Allows us to show the step number */
   list-style: none;
   padding: 0;
   margin: 60px 0;
   display: flex;
   flex-direction: row;
   /* Put the discs in a row */
   flex-wrap: wrap;
   /* Allow them to wrap around as required */
   justify-content: center;
}
.disc-list li {
   counter-increment: step;
   /* Increment the list number */
   position: relative;
   width: 8.5rem;
   height: 8.5rem;
   text-align: center;
   border-radius: 50%;
   /* Make it a circle */
   background: #888;
   color: #888;
   display: flex;
   flex-shrink: 0;
   /* Don't allow the discs to squash inside the container */
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin: 1.5rem 1.25rem 1.5rem 1.5rem;
   /* Right margin is the normal width - the border-width */
   overflow: visible;
}
.disc-list.static li:nth-child(1) {
   background: #00afc0;
   color: #00afc0;
}
.disc-list.static li:nth-child(2) {
   background: #00a3e1;
   color: #00a3e1;
}
.disc-list.static li:nth-child(3) {
   background: #27579c;
   color: #27579c;
}
.disc-list.static li:nth-child(4) {
   background: #934fc0;
   color: #934fc0;
}
.disc-list.static li:nth-child(5) {
   background: #c04f8e;
   color: #c04f8e;
}
.disc-list.static li:nth-child(6) {
   background: #c04f5d;
   color: #c04f5d;
}
.disc-list.static li:nth-child(7) {
   background: #c07e4f;
   color: #c07e4f;
}
.disc-list.static li:nth-child(8) {
   background: #c0b54f;
   color: #c0b54f;
}
.disc-list.static li:nth-child(9) {
   background: #86793c;
   color: #86793c;
}
.disc-list.static li:nth-child(10) {
   background: #486843;
   color: #486843;
}
.disc-list li span {
   color: #fff;
   display: block;
   font-weight: bold;
   font-size: 1.1rem;
   max-width: 5rem;
   margin-left: auto;
   margin-right: auto;
}
.disc-list li span::before {
   display: block;
   font-size: 1.65rem;
   margin-bottom: 0.1rem;
   font-weight: 200;
}
#user-levels li span::before {
   content: attr(data-count);
}
.disc-list.static li span::before {
   content: attr(data-percent);
}
.disc-list li svg {
   position: absolute;
   left: -1.5rem;
   top: -1.5rem;
   bottom: -1.5rem;
   /* Same as the disc-list li margin */
   right: -1.5rem;
}
.disc-list li svg path {
   stroke: currentcolor;
}

#app-loader {
   position: fixed;
   top: 0;
   left: 0;
   background-color: rgba(255, 255, 255, 0.9);
   width: 100vw;
   height: 100vh;
   z-index: 100000000;
   display: flex;
   justify-content: center;
   align-items: center;
   display: none;
}

